#main {
  display: -webkit-flex;
  display: flex;
  overflow-y: hidden; }
  #main .part {
    -webkit-flex: 1 1;
            flex: 1 1; }
    #main .part .section {
      width: 100%;
      height: 100vh;
      position: relative;
      overflow: hidden; }
      #main .part .section img {
        width: 100vw;
        height: 100vh;
        object-fit: cover;
        position: absolute;
        left: var(--x);
        pointer-events: none; }

.buttons {
  position: absolute;
  right: 25px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 99; }
  .buttons button {
    border: none;
    background-size: contain;
    background: url("data:image/svg+xml,%3Csvg width='10' height='29' viewBox='0 0 10 29' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9 0V27L1 17.4857' stroke='white' stroke-width='2' /%3E%3C/svg%3E%0A") no-repeat;
    background-position: center;
    width: 10px;
    height: 30px;
    display: block;
    margin: 20px 0;
    padding: 0 15px;
    transition-duration: .6s; }
    .buttons button.next {
      -webkit-transform: scaleY(-1);
              transform: scaleY(-1); }
    .buttons button.prev:active {
      -webkit-transform: translateY(8px);
              transform: translateY(8px); }
    .buttons button.next:active {
      -webkit-transform: scaleY(-1) translateY(8px);
              transform: scaleY(-1) translateY(8px); }

h1 {
